<template lang="pug">
#menuContainer
    router-link.logoContainer(to='/')
        .logo UbiComp Lab
        .subLogo @NTHU
    router-link.menuItem(v-for='(item,i) in menuList' :to="item.link" :key="i" :class="$route.path==item.link?'activate':''") {{item.name}}
        #underline
    .menuIcon(@click='opentheDrawer()' :class="menuclassList")
        .menuIconBar1
        .menuIconBar2
        .menuIconBar3
    #drawer(v-if="drawerStatus")
        router-link.drawerItem(v-for='(item,i) in menuList' :to="item.link" :key="i") {{item.name}}
</template>

<script>
export default {
    
    data () {
        return {
            menuList: [
                {name: "Home",link: "/"},
                {name: "Members",link: "/members"},
                {name: "Projects",link: "/projects"},
                {name: "Publications",link: "/publications"},
                {name: "News",link: "/news"},
                {name: "Videos",link: "/videos"},
                {name: "FAQs",link: "/FQAs"}
            ],
            drawerStatus: false,
            menuclassList: {
                menuIconContainer: true,
                change: false
            }
        }
    },
    watch:{
        '$route': function() {
            this.drawerStatus = false;
            this.menuclassList.change = false;
        }
    },
    mounted() {
    },
    methods: {
        opentheDrawer: function(){
            this.drawerStatus = !this.drawerStatus;
            this.menuclassList.change = !this.menuclassList.change;
        }
    }
}

</script>

<style lang="sass" scoped>
@import "@/style/value.sass"

#menuContainer
    display: flex
    flex-wrap: wrap
    padding: 10px
    background-color: $c-gray1
    color: $c-white
    @media all and (min-width: 960px)
        justify-content: center
    @media all and (max-width: 960px)
        justify-content: space-between
    
.logoContainer
    margin: 5px 50px 0 10px
    color: $c-white
    letter-spacing: 3px
    .logo
        margin-top: 5px
        font-size: 30px
        font-weight: 300
    .subLogo
        font-size: 12px
        letter-spacing: 5px
        text-align: center

.menuItem
    color: $c-white
    cursor: pointer
    margin: 20px
    font-weight: 300
    letter-spacing: 1px
    transition: 0.5s
.activate
    border-bottom: 1px $c-sec solid
.menuItem:hover
    color: #aaa
    #underline
        width: 100%
.menuIcon
    cursor: pointer
.menuIconContainer
    margin: 10px
.menuIconBar1, .menuIconBar2, .menuIconBar3
    width: 35px
    height: 3px
    background-color: $c-white
    transition: 0.3s
    margin: 8px
    position: relative
    border-radius: 25px
.change .menuIconBar1
    opacity: 0
    
.change .menuIconBar2 
    -webkit-transform: rotate(-30deg)
    transform: rotate(-30deg)

.change .menuIconBar3 
    -webkit-transform: rotate(210deg) 
    transform: rotate(210deg) translate(8px, 8px)
    transition-delay: 0.2s
    
#drawer
    background-color: $c-gray1
    width: 100%
    padding-top: 20px
    color: $c-white
    
.drawerItem
    cursor: pointer
    display: block
    text-align: center
    margin: 30px
    font-size: 20px
    color: $c-white

@media all and (min-width: 960px)
    .menuItem
        opacity: 1
        display: block
    .menuIcon,#drawer
        opacity: 0
        transition: 0.3s
        visibility: hidden
        display: none
    
@media all and (max-width: 960px)
    .menuItem
        opacity: 0
        transition: 0.3s
        visibility: hidden
        display: none
    .menuIcon,#drawer
        display: block


</style>



