<template>
  <div id="app">
    <menuTop class="menuTop"/>
    <transition name="fade">
      <router-view :newsList='newsList' class="content"/>
    </transition>
    <footerBom />
  </div>
</template>

<script>
import menuTop from "@/components/menu.vue"
import footerBom from "@/components/footer.vue"
export default {
  name: 'App',
  components: {
    menuTop,footerBom
  },
  data () {
    return {
        newsList: [
          {updated_at: '2021/11/30',title: 'The website of UbiComp Lab at National Tsing Hua University is up and running!!!',content: ''},
          {updated_at: '2021/11/30',title: 'The website of UbiComp Lab at National Tsing Hua University is up and running!!!',content: ''}
        ]
      }
    }
}
</script>

<style>
body {
  margin: 0;
}
.content {
  min-height: 90vh;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



