<template lang="pug">
#footerContainer
    .logoContainer
        .logoImg (LEGO)
        .logo UbiComp Lab
        .subLogo @NTHU
    .contactContainer
        h3 Contact Info
        .contact Interdisciplinary Program of Technology and Art<br>National Tsing Hua University<br>Nanda Campus<br>No. 521, Nanda Road, East District, Hsinchu 300145095, Taiwan<br>
        a.email(href = "mailto: nthu.ubicomp.lab@gmail.com") nthu.ubicomp.lab@gmail.com
    .copyright © 2021 UbiComp Laboratory
    
</template>

<script>
export default {
    
    data () {
        return {
            menuList: [
                {name: "Home",link: "/"},
                {name: "Members",link: "/members"},
                {name: "Projects",link: "/projects"},
                {name: "Publications",link: "/publications"},
                {name: "News",link: "/news"},
                {name: "Videos",link: "/videos"},
                {name: "FAQs",link: "/FQAs"}
            ],
            drawerStatus: false,
            menuclassList: {
                menuIconContainer: true,
                change: false
            }
        }
    },
    watch:{
        '$route': function() {
            this.drawerStatus = false;
            this.menuclassList.change = false;
        }
    },
    mounted() {
    },
    methods: {
        opentheDrawer: function(){
            this.drawerStatus = !this.drawerStatus;
            this.menuclassList.change = !this.menuclassList.change;
        }
    }
}

</script>

<style lang="sass" scoped>
@import "@/style/value.sass"

#footerContainer
    display: flex
    flex-wrap: wrap
    padding: 10px
    background-color: $c-gray1
    color: $c-white
    justify-content: center   
    
.logoContainer
    color: $c-white
    letter-spacing: 3px
    text-align: center
    .logo
        font-size: 30px
        font-weight: 300
    .subLogo
        font-size: 12px
        letter-spacing: 5px
.contactContainer
    width: 300px
    h3
        font-weight: bold
    .contact
        font-weight: 100
    .email
        color: $c-sec
.contactContainer,.logoContainer
    margin: 50px 25px 25px 25px
    @media all and (min-width: 480px)
        width: 480px
    @media all and (max-width: 480px)
        width: 100%
.copyright
    width: 100%
    text-align: center
    font-weight: 100
    margin: 20px
</style>



