<template lang="pug">
#App
    h1.TitleU {{name}}
    .staffContainer(v-for='pos in data')
        h1.chairTitle {{pos.title}}
        .staffListContainer
            .staffListContentBlock(v-for='item in pos.staff')
                a.staffListName(v-if='item.href!=""' :href="item.href" target='_blank') {{item.name}}
                .staffListName(v-if='item.href==""') {{item.name}}
                .staffListTitle {{item.title}}

</template>

<script>
export default {
    components: {
        
    },
    data () {
        return {
            name: "Members",
            data: [
                {title: "Faculty",
                staff: [
                    {name: 'Chuang-Wen You',title: 'Assistant Professor',href: "https://sites.google.com/site/cwyou2004/"}
                ]},
                {title: "Post-doctoral Researcher / Research Assistant",
                staff: [
                    {name: 'Min-Wei Hung',title: 'Research Assistant',href: ""}
                ]},
                {title: "Current Graduate Students",
                staff: [
                    {name: 'Hsueh-Sung Lu',title: 'MS Program at ISA',href: ""},
                    {name: 'Chieh-Jui Ho',title: 'Undergraduates at IPTA',href: ""},
                    {name: 'Chi-Ting Hou',title: 'Undergraduates at IPTA',href: ""},
                    {name: 'Pin-Chieh Chen',title: 'Undergraduates at IPTA',href: ""}
                ]}
            ]
        }
    },
    methods: {

    }
}
</script>

<style lang="sass" scoped>
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap')

.sraffPage
    padding-top: 100px
.TitleU
    text-align: center

    
.staffContainer
    max-width: 1080px
    margin: auto


@media (max-width: 960px)
    .staffListContentBlock
        width: 100%
    .staffListContainer
        width: 100%
    .staffContainer
        flex-flow: wrap
    .staffListContentBlock
        padding-top: 15px
        margin-top: 0 !important
    .chairTitle
        text-align: center
        
.staffListContentBlock
    margin: 30px 
    width: 270px
    margin-left: 40px

.staffListContainer
    display: flex
    flex-flow: wrap
    margin-bottom: 50px

.staffListImage
    width: 175px
    height: 175px
    border-radius: 8px
    margin: 25%
    background-position: center
.staffListName
    font-size: 19px
    font-weight: bold
    width: 100%
    font-family: 'Roboto', sans-serif
    color: #e8b279
    font-weight: bold
.staffListTitle
    font-size: 19px
    width: 100%
    font-family: 'Roboto', sans-serif
.chairTitle 
    width: 100%
</style>



