<template lang="pug">
#App
    h1.articlePageTitle {{post_data.title}}
    .dateNote {{post_data.updated_at}}
    //- .articlePageJumpbar(v-if='data.media' :style="{'background-image': 'url(' + post_data.media.info.src + ')'}")
    //- .HTMLContainer(v-html="post_data.body")
    div {{post_data.content}}


</template>

<script>

export default {
    props: ['newsList'],
    data() {
        return {
            post_data: {}
        }
    },
    created() {
        let post_idx = this.$route.params.id;
        this.post_data = this.newsList[post_idx];
    },
    methods: {
       
    }
}

</script>

<style lang="sass" scoped>
#newsApp
    max-width: 1280px
    margin: auto
.HTMLContainer
    max-width: 1280px
    margin: auto
@media all and (max-width: 760px)
    img
        width: 100%

.articlePageJumpbar
    background-size: cover
    max-width: 1280px
    height: 640px
    margin: auto
    background-position: center
#hashTag
    color: #777
    margin-right: 20px
.author,.dateNote
    margin: 10px
</style>


