<template lang="pug">
#PageContainer
    .slideContainer
        SlideShow
    .introContainer
        .blockTitle Our Mission
        .introText The UbiComp Lab at National Tsing Hua University incorporates interdisciplinary forces to synergetistically develop computational support to promote people’s health and safety in their daily lives.
    .newsContainer
        .blockTitle Recent news
        .newsTitleBlock(v-for="(item,index) in newsList" :key='index')
            .newsDate {{item.updated_at}}
            .newsTitle {{item.title}}
            .newLine
    .aboutContainer
        .blockTitle About Us
        .aboutText The UbiComp Lab (@NTHU) focuses on designing, building and evaluating high-impact HCI & UbiComp technologies that really apply computing technologies naturally into people’s everyday activities. Specifically, we investigated on building mobile sensing platforms that sense and understand daily behavior, promote healthy behavior (e.g., assisting people with alcohol-addicted, drug-addicted, technology-abuse or gambling-addicted problems in recovery from their addicted or abused behaviors) and facilitate people’s everyday activities (e.g., shopping, driving, parking, and social activities), and improve their real-life safety (e.g., avoiding recommitting drunk driving). 
</template>

<script>
import SlideShow from '@/components/SlideShow.vue'
export default {
    props: ['newsList'],
    components: {
        SlideShow
    },
    data () {
        return {
            intro_img: ''
        }
    }
}
</script>


<style lang="sass" scoped>
@import "@/style/value.sass"
#PageContainer
    display: flex
    flex-wrap: wrap
    justify-content: center

.slideContainer,.aboutContainer
    width: 100%
    background-color: $c-gray3

.slideContainer
    height: 450px
.introContainer,.newsContainer
    width: 30%
    padding: 40px 20px 70px 20px
    max-width: 640px
    @media all and (min-width: 480px)
        min-width: 400px
    @media all and (max-width: 480px)
        width: 100%

.newsTitleBlock
    margin-bottom: 10px
.newsDate
    margin-bottom: 5px
    color: $c-gray1
.newsTitle
    margin-bottom: 10px
.newLine
    width: 150px
    height: 1px
    background-color: $c-gray3
.aboutContainer
    padding: 50px
    .blockTitle
        margin: 20px auto
        white-space: nowrap
.aboutText
    width: 80%
    max-width: 1280px
    margin: 50px auto
</style>



