<template lang="pug">
#SlideShow
    #sliceshowContainer(:style="{'background-image': 'url('+pages[idx].image+')'}")
        .sectionContainer(v-if="pages[idx]")
            .sectionTitle {{pages[idx].title}}
            .sectionText {{pages[idx].content}}
    .slideDotContainer
        .slideDot(v-for="dotIdx in pagesNum" :class="dotIdx==idx+1?'activite':''")
</template>

<script>
var slide;
export default {
    data () {
        return{
            idx: 0,
            isLoad: false,
            pages: [
                {image:require('@/assets/cravingprobe_banner.png'),title:'TechLifeProbe1',content:"Using Mobile and Wearable Technologies to Improve Adolescents' Smartphone Addiction Through Sharing Personal Data with Their Parents",link:''},
                {image:require('@/assets/cravingprobe_banner.png'),title:'TechLifeProbe2',content:"Using Mobile and Wearable Technologies to Improve Adolescents' Smartphone Addiction Through Sharing Personal Data with Their Parents",link:''},
                {image:require('@/assets/cravingprobe_banner.png'),title:'TechLifeProbe3',content:"Using Mobile and Wearable Technologies to Improve Adolescents' Smartphone Addiction Through Sharing Personal Data with Their Parents",link:''}
            ],
            pagesNum: 3,
        }
    },
    methods: {
        init() {
            this.pagesNum = this.pages.length;
            slide = setInterval(() => {
                if(this.idx<this.pagesNum-1){
                    this.idx+=1;
                }
                else{
                    this.idx=0;
                }
                // this.$refs.slideshow.goTo(this.idx);
            }, 4000);
        },
        changePath: function(idx){
            this.$router.push({ path: '/works/post/'+ idx})
        },
    },
    created() {
        this.init();
        this.isLoad = true;
    },
    beforeDestroy(){
        clearInterval(slide);
    }
}
</script>

<style lang="sass" scoped>
#SlideShow
    padding: 40px 0 20px 0
#sliceshowContainer
    margin: auto
    height: 360px
    max-width: 1280px
    background-size: cover
    border-radius: 16px
    @media all and (min-width: 746px)
        width: 80%
    @media all and (max-width: 746px)
        width: 100%
.sectionContainer
    width: 40%
    height: 50%
    position: relative
    left: 50%
    top: 25%
    background-color: #0006
    color: #FFF
    border-radius: 16px
    padding: 20px
    .sectionTitle
        font-size: 24px
        letter-spacing: 2px
        font-weight: 300
    .sectionText
        border-left: 2px #FFDF6C solid
        padding: 10px
        margin-top: 10px
.slideDotContainer
    margin: 20px auto
    width: fit-content
    display: flex
    .slideDot
        width: 10px
        height: 10px
        border-radius: 10px
        border: 1px #707070 solid
        margin: 0 5px
    .activite
        background-color: #707070
</style>



